.wrapper{
  color: white;
  display: flex;
  justify-content:flex-start;
  flex-direction: column;
  border: 1px rgb(60, 60, 60) solid;

  gap: 1rem;
 padding: 2rem;
}
.container1{
  display: flex;
  flex-direction: row;
  justify-content:flex-start;
  gap: 1rem;
}
.icons{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  color: #6A6F74;
}
.img{
text-align: center;

}
.avatar{
  z-index: -1;
}
@media screen and (max-width:450px) {
  .wrapper{
     width: 100%;
   }
  .container1{
    height: 10rem;
    width: 20rem;
  }
  .img{
    height: auto;
    width: auto;
  }
}
