.wrapper{
  color: white;
  display: flex;
  justify-content:flex-start;
  flex-direction: column;
  border: 1px rgb(60, 60, 60) solid;

  gap: 1rem;
 padding: 2rem;
}
.container1{
  display: flex;
  flex-direction: row;
  justify-content:flex-start;
  gap: 1rem;
}
.Dialog{
  backdrop-filter: blur(5px);
}
.icons{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  color: #6A6F74;
}
.img{
padding-left: 4rem;

}
.avatar{
  z-index: -1;
}

@media screen and (max-width:450px){
.wrapper{
  padding-top: 6rem;

}

}
